<template>
    <div id="xiaoyou">
        <el-main >
            <el-select style="float:left; margin-left:70px;" v-model="value" placeholder="巴金" 
                @change="changeSubPartIndex">
                <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>

            <el-table :data="commentTable" style="width: 100%" row-key="id" stripe>
                <!-- <el-table-column prop="id" label="ID" ></el-table-column> -->
                <el-table-column prop="commentDate" label="评论日期" min-width="20%"></el-table-column>
                <el-table-column prop="commentString" label="评论内容" min-width="60%"></el-table-column>
                <el-table-column fixed="right" label="操作" min-width="20%">
                    <template slot-scope="scope">
                        <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">编辑</el-button>

                        <template>
                            <el-popconfirm
                            confirm-button-text='确定'
                            cancel-button-text='取消'
                            icon="el-icon-info"
                            icon-color="red"
                            title="本条评论将被删除！"
                            @onConfirm="handleDelete(scope.$index, scope.row)"
                            >
                            <el-button 
                                slot="reference" 
                                size="mini"
                                type="danger"
                                style="margin-left:20px"
                                >删除</el-button>
                            </el-popconfirm>
                        </template>                                                
                                                
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                background
                layout="prev, pager, next"
                class="pagination"
                style="text-align: center"
                :page-size="pageSize"
                :total="total"
                @current-change="pageChange"
            ></el-pagination>
        </el-main>
    </div>
</template>

<script>
export default {
    data() {
        return {
            pageNo: 1,
            pageSize: 5,
            partId: 5,
            subPartIndex: 1,
            total: 0,
            manager: this.$route.params.manager,
            commentTable: [],

            options: [{
                value: '1',
                label: '巴金'
                }, {
                value: '2',
                label: '郭沫若'
                }, {
                value: '3',
                label: '江姐'
                }, {
                value: '4',
                label: '熊复'
                }, {
                value: '5',
                label: '朱德'
            }],

            value: ''
        };
    },
    components: {},
    //方法
    methods: {
        dataFormat(num) {
            let dd = new Date(num);
            let y = dd.getFullYear();
            let m = dd.getMonth() + 1;
            let d = dd.getDate();
            let h = dd.getHours();
            let i = dd.getMinutes();
            let s = dd.getSeconds();
            m = m < 10 ? '0' + m : m;
            d = d < 10 ? '0' + d : d;
            h = h < 10 ? '0' + h : h;
            i = i < 10 ? '0' + i : i;
            s = s < 10 ? '0' + s : s;
            return y + '-' + m + '-' + d + ' ' + h + ':' + i + ':' + s
        },
        handleEdit: function (index, row) {
            var that = this;
            // var newComment = prompt("请适度修改评论",row.commentString);
            // console.log(newComment);
            this.$prompt('请适度修改评论:)', '修改评论', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                inputPlaceholder: row.commentString,
                }).then(({ value }) => {
                    this.$message({
                        type: 'success',
                        message: '修改后的评论是: ' + value,
                    });
                    that.commentTable[index].commentString = value;
                    that.update(row);

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '取消输入'
                    });       
                });
            
        },

        update:function(row){
            axios
                .post("http://muzi.fun:8765/campus/comment/update", {
                    id:row.id,
                    commentString:row.commentString
                })
                .then(function (response) {
                    console.log(response.data);
                })
                .catch(function(error){
                    console.log(error);
                });
        },

        handleDelete: function (index, row) {
            console.log(1111111);

            var that = this;
            axios
                .post("http://muzi.fun:8765/campus/comment/delete", {
                    id:row.id
                })
                .then(function (response) {
                    console.log(response.data);
                    that.commentTable.splice(index, 1);
                })
                .catch(function(error){
                    console.log(error);
                });
        },
        pageChange(currentPage) {
            this.pageNo = currentPage;
            var that = this;
            axios
                .post("http://muzi.fun:8765/campus/comment/getCommentPage", {
                    pageNo: that.pageNo,
                    pageSize: that.pageSize,
                    partId: that.partId,
                    subPartIndex : that.subPartIndex
                })
                .then(function (response) {
                    console.log(response.data);
                    that.commentTable = response.data.list;
                    that.total = response.data.total;
                    for (let j = 0; j < that.commentTable.length; j++) {
                        that.commentTable[j].commentDate = that.dataFormat(Number(that.commentTable[j].commentDate));                        
                    }
                })
                .catch(function(error){
                    console.log(error);
                });
        },
        changeSubPartIndex(){
            this.subPartIndex = this.value;
            this.pageChange(1);
        },
        
    },
    //生命周期钩子
    created: function () {
        this.pageChange(1)
    },
};
</script>

<style>
#departments{
    font-family: "Microsoft YaHei";
    text-align: center;
    display: flex;
}
.pagination{
    margin: 40px;
}
</style>
